@import "../../colors.less";

//== Global ==
//== a tag custom style
a {
  border-bottom: 1px dashed @storm;
}
a:hover {
  border-bottom: 1px solid @silver;
}

em {
  color: @green-sea;
  font-weight: bold;
  font-style: normal;
}

.big-data-header {
  font-size: 30px;
  font-weight: bold;
}

nav a,
nav a:hover,
.header a,
.header a:hover,
.breadcrumb a,
.breadcrumb a:hover {
  border-bottom: none;
}

//== Buttons
.btn:active,
.btn.active {
    box-shadow: none;   
}

button.btn {
  font-size: 16px;
  padding: 4px 30px;
}

button.btn.btn-lg {
  padding: 12px 30px;
}

//** Scope Bar styling
label.btn.btn-scope {
    background-color: @white;  
    color: @storm;
    border-color: @storm;
    border-radius: @btn-scope-border-radius;
    font-size: 14px;
    padding: 0 10px 0 10px;

      &:hover {
        color: @white;
        background-color: @silver;
        border-color: @storm;
      }
    
      &:active,
      &.active,
      .open > .dropdown-toggle& {
        color: @storm;
        background-color: @clouds;
        border-color: @storm;

        &:hover,
        &:focus,
        &.focus {
          color: @storm;
          background-color: @clouds;
          border-color: @storm;
        }
      }

      &.disabled,
      &[disabled],
      fieldset[disabled] & {
        &,
        &:hover,
        &:focus,
        &.focus,
        &:active,
        &.active {
          color: @platinum;
          background-color: @white;
          border-color: @storm;
        }
      }
}

//== List Group
.panel .list-group {
  padding: 0 10px 10px 10px;

  .list-group-title {
    font-weight: bold;
    font-size: 18px;
  }

  .list-group-item {
    padding: 10px;
  }

  .list-group-item:first-child {
    padding-top: 0;
  }
}

//== Modal
.modal-dialog {

  .modal-header {
    padding-bottom: 10px;

    .modal-title {
      font-size: 22px;
      display: inline;
    }
  }

  .modal-body {
    padding-left: 15px;
    padding-bottom: 10px;

    .modal-title {
      font-size: 20px;
      color: @storm;
      margin-bottom: 10px;
    }

    .modal-title:not(:first-child) {
      padding-top: 20px;
    }
  }

  .modal-footer {
    padding-bottom: 15px;

    .btn:last-child {
      margin-left: 10px;
    }
  }

  .modal-title .modal-title-description {
    font-size: 14px;
    margin-left: 10px;
    color: @concrete;
  }

}

.modal-dialog.modal-xl {
  width: 1030px;
}

.modal-dialog.modal-wizard {
  width: 790px;
  height: 600px;

  .modal-content {
    height: 100%;
  }

  .modal-header {
    height: 10%;
    border-bottom: 1px solid @platinum;
    padding: 15px 10px 12px 15px;
  }

  .modal-wizard-container {
    position: relative;
    height: 90%;
    margin: 0;

    .modal-wizard-sidebar {
      position: absolute;
      bottom: 0;
      top: 0;
      width: 220px;
      border-right: 1px solid @platinum;
      background-color: @clouds;

      ul {
        list-style-type: none;

        li {
          padding: 15px 17px;
          cursor: pointer;
        }

        li.modal-wizard-step-active {
          font-weight: bold;
          background-color: @white;
        }
      }
    }

    .modal-wizard-body {
      display: inline-block;
      position: relative;
      left: 220px;
      height: 100%;

      .modal-body {
        width: 568px;
        height: 100%;
        overflow: auto;
        padding: 0;

        .modal-title {
          padding: 20px 20px 0px 20px;
          margin: 0;
        }

        .modal-wizard-step-content {
          /**
           * Padding of the parent container is ignored in FF if overflow is activated
           * So put a padding on the content instead.
           * 62px for the footer, 52px + 10px padding
           */
          padding: 10px 32px 62px 32px;
        }
      }

      .modal-footer {
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 52px;
        background: #FFF;
        padding: 10px 0 10px 10px;
        border-top: 1px solid @platinum;

        .btn {
          float: right;
          margin-right: 10px;
          margin-left: 0;
        }
      }
    }
  }
}

.modal.in[animate] .modal-dialog, .modal.fade[animate] .modal-dialog {
  -webkit-transform: none;
  -ms-transform: none;
  -o-transform: none;
  transform: none;
}
.modal.fade[animate] .modal-dialog {
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
}


//== Form states and alerts
// note that some alert style is from web3 because we cannot easily eliminate
// web3 css when it use ID which is more specific than just class name here.
.alert[role] {
  font-size: 12px;
  margin: 0px 10px 10px 10px;
  border-width: 1px 1px 1px 14px;
}

.alert-title {
  font-weight: bold;
  font-size: 14px;
  margin: 0 0 5px 0;
}

.alert-danger a {
  color: @pomegranate;
}

.alert-danger a:hover {
  color: @tomato;
}


//== Table styles for both use as a form or report (not in Bootstrap)
.tbl-container {

  ul {
    list-style-type: none;
  }

  .tbl-message {
    font-style: italic;
    text-align: center;
  }

  ul.tbl-row,
  ul.tbl-header {
    margin-left: 0;
    padding-bottom: 5px;
    padding-top: 5px;
    border-bottom: 1px solid @platinum;
  }

  ul.tbl-header {
    margin-bottom: 0;
    border-bottom: 0;
    padding-top: 0;
    padding-bottom: 0;
    background-color: @clouds;
  }

  ul.tbl-row.tbl-row-message {
    color: @concrete;
    padding-top: 15px;
    padding-bottom: 15px;
  }

  ul.tbl-row-group {
    padding-top: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid @platinum;
  }

  ul.tbl-row-group > li > ul.tbl-row {
    border-bottom: 0;
    padding-top: 3px;
    padding-bottom: 3px;
  }

  .tbl-row-selected {
    background-color: @clouds;
  }

  /* different level of indent for tree structure */
  li.tbl-level-1 { padding-left: 40px; }
  li.tbl-level-2 { padding-left: 80px; }
  li.tbl-level-3 { padding-left: 120px; }
  li.tbl-level-4 { padding-left: 160px; }
  li.tbl-level-5 { padding-left: 200px; }
  li.tbl-level-6 { padding-left: 240px; }
  li.tbl-level-7 { padding-left: 280px; }
  li.tbl-level-8 { padding-left: 320px; }
  li.tbl-level-1.tbl-level-last { padding-left: 23px; }
  li.tbl-level-2.tbl-level-last { padding-left: 63px; }
  li.tbl-level-3.tbl-level-last { padding-left: 103px; }
  li.tbl-level-4.tbl-level-last { padding-left: 143px; }
  li.tbl-level-5.tbl-level-last { padding-left: 183px; }
  li.tbl-level-6.tbl-level-last { padding-left: 223px; }
  li.tbl-level-7.tbl-level-last { padding-left: 263px; }
  li.tbl-level-8.tbl-level-last { padding-left: 303px; }

}

.tbl-container.tbl-report {

  li.tbl-level-2 {
    padding-left: 72px;
  }

}

//
// Forms
// --------------------------------------------------

/* form-control widgets should have a default width of 150px to match web3 */
.form-control {
  width: 150px;
}
/*
If it's an expandable textarea box, it should start out as wide
as the div allows.
*/
textarea.form-control {
  width: 100%;
}


label {
  font-weight: normal !important;
}

.form-horizontal label.control-label {
  text-align: left;
}


//
// Error tooltip
// --------------------------------------------------

.tooltip.top-left .tooltip-arrow {
  left: 10px !important;
  margin-bottom: 0px !important;
}

.error-tooltip.tooltip > .tooltip-inner {
  background-color: #BF382A;
  color: #FFF;
}
.error-tooltip.tooltip.top > .tooltip-arrow {
  border-top-color: #BF382A;
}

.tooltip.left .tooltip-arrow {
  border-left-color: #BF382A;
}

input.invalid:focus {
  border-color: #BF382A;
  box-shadow: inset 0 1px 1px rgba(255, 0, 0, 0.075), 0 0 8px rgba(255, 0, 0, 0.6);
}

//== Message styles
.message-confirm,
.message-confirm .ri,
.message-confirm .ri:hover {
  color: @green;
  cursor: inherit;
}

.message-error,
.message-error .ri,
.message-error .ri:hover {
  color: @pomegranate;
  cursor: inherit;
}

.message-error,
.message-confirm {
  text-indent: -26px;
  padding-left: 26px;

  .ri {
    text-indent: 0;
  }
}

.message-error .ri {
  margin-right: 6px;
}

